"use client";

import { useState } from "react";
import ReactMarkdown from "react-markdown";
import { Alert, Button, Card, Col, Form, Input, Row, Space, Tag, Typography } from "antd";

const seedPrompt = "Summarize rollout risk for template pages and recommend mitigations.";

export default function AssistantPage() {
  const [prompt, setPrompt] = useState(seedPrompt);
  const [answer, setAnswer] = useState("");
  const [loading, setLoading] = useState(false);
  const [citations, setCitations] = useState<Array<{ source: string; section: string }>>([]);

  async function runAssistant() {
    setLoading(true);
    await new Promise((resolve) => setTimeout(resolve, 700));
    setAnswer(
      [
        "## Executive Summary",
        "",
        "- UI consistency risk remains if route wrappers diverge.",
        "- Auth/session drift causes false-negative data fetch failures.",
        "- Template pages need stronger component reuse boundaries.",
        "",
        "## Recommended Actions",
        "",
        "1. Use shared route guards and API client wrappers.",
        "2. Add template smoke tests to CI for all route surfaces.",
        "3. Standardize on enterprise UI library components.",
      ].join("\n"),
    );
    setCitations([
      { source: "docs/codex-migration/template-pages-functionalization-plan.md", section: "Library selections" },
      { source: "templates/ai-substrate/app/layout.tsx", section: "Navigation + route grouping" },
      { source: "templates/ai-substrate/app/dashboard/page.tsx", section: "Dashboard fetch handling" },
    ]);
    setLoading(false);
  }

  return (
    <div className="p-6 space-y-4">
      <Space orientation="vertical" size={4}>
        <Typography.Title level={2} className="!mb-0">RAG Assistant Template</Typography.Title>
        <Typography.Text type="secondary">Ant Design form + markdown rendering + citation drawer pattern.</Typography.Text>
      </Space>

      <Card>
        <Form layout="vertical" onFinish={() => void runAssistant()}>
          <Form.Item label="Prompt">
            <Input.TextArea rows={5} value={prompt} onChange={(event) => setPrompt(event.target.value)} />
          </Form.Item>
          <Space>
            <Button type="primary" htmlType="submit" loading={loading}>Run Assistant</Button>
            <Tag color="blue">Scoped retrieval</Tag>
            <Tag color="green">Traceable output</Tag>
          </Space>
        </Form>
      </Card>

      <Row gutter={[16, 16]}>
        <Col xs={24} lg={16}>
          <Card title="Answer">
            {answer ? (
              <div className="prose prose-sm max-w-none">
                <ReactMarkdown>{answer}</ReactMarkdown>
              </div>
            ) : (
              <Alert type="info" showIcon title="Run the assistant to generate a response." />
            )}
          </Card>
        </Col>
        <Col xs={24} lg={8}>
          <Card title="Citations">
            <Space orientation="vertical" className="w-full" size={10}>
              {citations.length === 0 ? (
                <Typography.Text type="secondary">No citations yet</Typography.Text>
              ) : (
                citations.map((item) => (
                  <Card key={`${item.source}-${item.section}`} size="small">
                    <Space orientation="vertical" size={0}>
                      <Typography.Text strong>{item.source}</Typography.Text>
                      <Typography.Text type="secondary">{item.section}</Typography.Text>
                    </Space>
                  </Card>
                ))
              )}
            </Space>
          </Card>
        </Col>
      </Row>
    </div>
  );
}
